import './App.scss';
import React from 'react';
import { Loader, LoadingAnimation } from './components/components';
import { Home, Dashboard } from './pages/pages';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

/**
 * App component
 */
class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { loader: null, callback: null, errorMessage: 'api.alexlipianu.com is currently unavailable or cannot be reached' };
    this.load = this.load.bind(this);
  }

  /**
   * Updates load promise
   */
  load(loader, callback) {
    this.setState({ loader, callback });
  }

  /**
   * Renders app
   */
  render() {
    return (
      <Loader loader={this.state.loader} callback={this.state.callback} animation={LoadingAnimation} errorMessage={this.state.errorMessage}>
        <Home load={this.load} />
      </Loader>
    );


    // return (
    // //<Dashboard />
    //   <Router>
    //     <Loader loader={this.state.loader} callback={this.state.callback} animation={LoadingAnimation} errorMessage={this.state.errorMessage}>
    //       <Switch>
    //         <Route path="/">
    //           <Home load={this.load} />
    //         </Route>
    //       </Switch>
    //     </Loader>
    //   </Router>
    // );
  }
}

export default App;
