// import './home.scss';
import React from 'react';
import analytics from '../general/analytics';
import request from '../general/request';
import { Footer } from '../components/components';
import {
  SplashContainer,
  AboutContainer,
  TimelineContainer,
  SkillsContainer,
  ExperienceContainer,
  ProjectsContainer,
  PensContainer,
} from '../containers/containers';
import config from '../config.json';


/**
 * Home component
 */
class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = { contentID: 0 };
  }

  componentDidMount() {
    this.props.load(() => request.post(`static/content/${this.state.contentID}/version/${config.version.replace(/\./g, '0')}`), this.loadData.bind(this));
  }

  componentWillUnmount() {
    analytics.end();
  }

  loadData(response) {
    this.setState({data: response.data}, () => analytics.start(this.state.contentID));
  }

  /**
   * Renders home
   */
  render() {
    return <>
      {
        this.state.data && (
          <>
          <SplashContainer {...this.state.data.splash} />
          <AboutContainer {...this.state.data.about} />
          <TimelineContainer {...this.state.data.timeline} />
          <SkillsContainer {...this.state.data.skills} />
          <ExperienceContainer {...this.state.data.experience} />
          <ProjectsContainer {...this.state.data.projects} />
          <PensContainer {...this.state.data.pens} />
          <Footer />
          </>
        )
      }
    </>;
  }
}

export default Home;
