import request from './request';
import config from '../config';
import { Subject } from 'rxjs';

// outside Analytics class because when when beacon is sent, analytics object has already been destroyed
let _session = null;

/**
 * Analytics class
 */
class Analytics { 
  /**
   * Create analytics
   */
  constructor() {
    this.trackLength = 0;
    this.tracking = {};
    this.analytics = new Subject();
  }
  
  /**
   * Start session
   * @param {string} contentID the content ID
   * @returns {Promise} the session start promise
   */
  start(contentID) {
    // start tracking
      let data = [];
      let lastSentTime = Date.now();
      // start tracking event analytics
      this.analytics.subscribe({next: ({event, item, amount}) => {
        if (!data.length) {
          setTimeout(() => {
            request.post(`analytics/content/${contentID}/version/${config.version}`);
            data = [];
            lastSentTime = Date.now();
          }, config.heartbeat);
        }

        // add analytic entry if does not exist or is outdated
        if (!item.analyticsId || item.lastUpdateTime <= lastSentTime) {
          item.analyticsId = data.length;
          data.push({ name: item.name, tags: item.tags, events: {} });
        }

        // record event in analytics data
        let events = data[item.analyticsId].events;
        events[event] = events[event] ? events[event] + amount : amount;
        item.lastUpdateTime = Date.now();
    }});
  }

  /**
   * End session
   */
  end() {
    this.analytics.unsubscribe();
  }

  /**
   * Element to tracking
   * @param {String} name element name
   * @param {string[]} tags tags used to identify/categorize element
   * @returns {number} the tracking id
   */
  track({name, tags}) {
    if (name || tags) {
      const _id = this.trackLength++;
      this.tracking[_id] = { name: name || '', tags: tags || '', lastUpdateTime: Date.now() };
      return _id;
    }
    return null;
  }

  /**
   * Record element event
   * @param {String} event the name of the event being recorded
   * @param {number} id the trackng id of the element for which the event is being recorded
   * @param {number} amount the amount to increment the event's value
   */
  record(event, id, amount = 1) {
    this.analytics.next({event, item: this.tracking[id], amount});
  }


  /**
   * Decorate existing analytics object
   * @param {*} parentAnalytics the parent's analytics
   * @param {*} childAnalytics the child's analytics
   * @param {Array<string>||string} events the events to track
   */
  decorate(parentAnalytics = {}, childAnalytics = {}, events = null) {
    return {
      name: (parentAnalytics.name || '') + (parentAnalytics.name && childAnalytics.name ? '-' : '') + (childAnalytics.name || ''),
      tags: (parentAnalytics.tags || '') + (parentAnalytics.tags && childAnalytics.tags ? ' > ' : '') + (childAnalytics.tags || ''),
      events
    };
  }
}

export default (new Analytics());